<template>
  <div class="search-container">
    <div class="search-input-wrapper">
      <input type="text" placeholder="搜索音乐" v-model="searchText" @keydown="searchKeydown">
      <a href="javascript:;" class="shadow-btn icon-btn search-btn" @click="search"></a>
    </div>
    <div class="result-area" v-bottom-load="loadMore">
      <div class="song-list" v-for="song in result" :key="song.contentId">
        <div class="list-left">
          <span class="song-name">{{song.name}}<span class="singer-name" v-for="singer in song.singers" :key="singer.id">{{singer.name}}</span></span>
          <span v-if="song.albums">《{{song.albums[0].name}}》</span>
        </div>
        <div class="list-right">
          <a href="javascript:;" class="shadow-btn icon-btn play-btn" @click="play(song)"></a>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div v-show="loading" class="loading-bar shadow-btn iconfont"><i>&#xe65d;</i><span>加载中</span></div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchText: '米兰的小铁匠',
      result: [],
      page: 1,
      pageSize: 10,
      loading: false
    }
  },
  created() {
    this.search()
  },
  methods: {
    searchKeydown($event) {
      if ($event.keyCode === 13) {
        this.search()
      }
    },

    search() {
      this.page = 1
      this.$http.get(`http://pd.musicapp.migu.cn/MIGUM3.0/v1.0/content/search_all.do?&ua=Android_migu&version=5.0.1&text=${this.searchText}&pageNo=${this.page}&pageSize=${this.pageSize}&searchSwitch={"song":1,"album":0,"singer":0,"tagSong":0,"mvSong":0,"songlist":0,"bestShow":1}`).then(res => {
        this.result = res && res.songResultData && res.songResultData.result
      })
    },

    play(song) {
      this.$emit('play', song)
    },

    loadMore() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.$http.get(`http://pd.musicapp.migu.cn/MIGUM3.0/v1.0/content/search_all.do?&ua=Android_migu&version=5.0.1&text=${this.searchText}&pageNo=${this.page}&pageSize=${this.pageSize}&searchSwitch={"song":1,"album":0,"singer":0,"tagSong":0,"mvSong":0,"songlist":0,"bestShow":1}`).then(res => {
        let result = res && res.songResultData && res.songResultData.result
        this.result = [...this.result, ...result]
      }).catch(error => {
        console.log(error)
        this.page--
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

